html {
  overflow-y: auto;
}

@font-face {
  font-family: 'Rubik Mono One';
  src: url('./assets/fonts/RubikMonoOne-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}


.App {
  height: 100vh;
  display:flex;
  flex-direction: row;
  justify-content: flex-start;
}

.page-body {
  width: 100%;
  margin: 0 auto;
  flex-grow: 5;
  padding-top: 50px;
  padding-left: 50px;
  background: teal; 
  overflow-y: auto;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
}

h3, h4 {
  margin-bottom: 0;
}

.nav-root {
  flex-grow: 1;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  height: 100vh;
  width: 250px;
  background:whitesmoke;
  box-shadow: 5px, 5px, 5px, 5px, white;
}

.nav-homebar {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 10px;
  margin: 0 auto;
  width: 50px;
  height: 100%;
  background-image: linear-gradient(90deg, transparent 90%, black 100%), url(./assets/black-perf-tile.png);
}

.nav-homebar-nameplate {
  font-family: Rubik Mono One;
  display: flex;
  flex-basis: 300px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  writing-mode:sideways-lr;
  margin: 0 auto;
  width: 100%;
}

.nav-homebar-nameplate h3 {
  color: rgb(191, 191, 191);
  font-size: 24px;
}

.nav-homebar-button {
  margin: 0 auto;
  width: 30px;
  height: 30px;
  background: orange;
}

.nav-profile {
  margin: 20px;
  height: 150px;
  width: 150px;
  background: blue;
}

nav {
  display: flex;
  flex-direction: column;
  background:
    linear-gradient(
      200deg,
      transparent 80%,
      rgba(255, 255, 255, 0.2) 100%
    ),
    linear-gradient(
    90deg,
    rgb(100,100,100) 0%,  
    rgb(30, 30, 30) 3%,
    rgb(30, 30, 30) 97%,
    rgb(100,100,100) 100%
    );
}

nav ul {
  list-style-type: none;
  padding-left: 0;
  width: 100%;
  margin: 0 auto;
}

nav li {
  background: linear-gradient(90deg, slategray 0%, black 3%, black 98%, slategray 99%); 
  width: 100%;
  text-align: right;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 0;
  border-top: 1px outset #F5F5F5;
  border-bottom: 3px outset #F5F5F5;
  font-family: 'Rubik Mono One';
  font-size: 16px;
}

nav li:hover {
  background: linear-gradient(90deg, slategray 0%, rgb(60, 60, 60) 3%, rgb(60, 60, 60) 98%, slategray 99%); 
}

nav a {
  text-decoration: none;
  color: white;
  font-weight: bold;
  padding-right: 40px;
}
.reflect-list-item {
  font-size: 24px;
  text-decoration: none;
  color: black;
  padding: 16px 0 0 0;
}

.reflect-list-item-p {
  font-size: 18px;
  text-decoration: none;
  color: black;
  padding-bottom: 32px;
}

.nav-right {
  position: absolute;
  top: 8px;
  right: 8px;
}


/* TIMELINE
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.timeline {
  margin: 0 auto;
  max-width: 1000px;
  color: #9033ff;
}

.timeline ol {
  position: relative;
  list-style: none;
}

.timeline ol::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 3px;
  height: 100%;
  background: #69f6da;
}

.timeline ol li .time-wrapper {
  padding-top: 20px;
  position: absolute;
  left: 20px;
  font-size: 4.5rem;
  font-weight: bold;
  line-height: 0.9;
  z-index: 1;
}

.timeline ol li .time-wrapper::before,
.timeline ol li .time-wrapper::after {
  content: "";
  position: absolute;
  background: #69f6da;
}

.timeline ol li .time-wrapper::before {
  top: 50%;
  left: -180px;
  transform: translateY(-50%);
  width: 0;
  height: 3px;
  transition: width 0.8s linear;
}

.timeline ol li .time-wrapper::after {
  top: calc(50% - 8px);
  left: -60px;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  transform: scale(0);
}

.tl-year {
  padding: 20px 180px;
  border-color: red;
  border-style: solid;
  border-width: 2px;
  margin: 10px;
}

.tl-year h3 {
  font-size: 2rem;
  line-height: 1;
  margin: 15px 0;
}

.tl-semester {
  display: flex;
  margin-top: 5px;
  position: relative;
  left: -150px;
  width: 700px;
  border-color: blue;
  border-style: dotted;
  border-width: 3px;
  background-color: blanchedalmond;
  z-index: 0;
}

.tl-semester-label {
  background-color: cornflowerblue;
  padding-top: 50px;
  margin-right: 30px;
  writing-mode:vertical-lr;
  text-orientation: upright;
}

.tl-semester-classes {
  background-color: aquamarine;
  flex-direction: column;
}

.tl-class {
  margin-top: 10px;
  padding: 10px;
  width: 600px;
  background-color: gainsboro;
  border-style: solid;
  border-color: black;
  border-width: 1px;
  border-radius: 15px 50px;
}